import pokedex from "../img/p1.png";
import coursite from "../img/p2.png";
import todo from "../img/p3.png";
import spring from "../img/p4.png";
import bookmark from "../img/p5.png";

import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import "swiper/css/navigation";
import "swiper/css/pagination";

import SwiperCore, {
    Navigation, Mousewheel, Keyboard, Pagination
} from 'swiper';

import { PortfolioItem } from './PortfolioItem';

SwiperCore.use([Navigation, Mousewheel, Keyboard, Pagination]);


export const Portfolio = () => {

    return (
        <section className="portfolio section div-bg" id="portfolio">
            <h2 className="section__title">Portafolio</h2>
            <span className="section__subtitle">Mis ltimos trabajos</span>

            <Swiper
                cssMode={true} navigation={true}
                pagination={true}
                className='portfolio__container container'>

                <SwiperSlide>
                    <PortfolioItem
                        img={pokedex}
                        title="Pokdex"
                        desc="Aplicacin de registro de pokmons desarrollada bajo un MERN stack."
                        link="https://pokemon.marco-cortes.me"
                        repo="https://github.com/marco-cortes/mern-pokedex" />
                </SwiperSlide>

                <SwiperSlide>
                    <PortfolioItem
                        img={coursite}
                        title="Coursite"
                        desc="PWA de una plataforma de aprendizaje desarrollada con React y Spring Boot."
                        link="https://app-coursite.web.app"
                        repo="https://github.com/marco-cortes/coursite" />
                </SwiperSlide>

                <SwiperSlide>
                    <PortfolioItem
                        img={todo}
                        title="TODO"
                        desc="Aplicacin para guardar lista de tareas, desarrollada con React."
                        link="https://marco-cortes-mern.herokuapp.com/"
                        repo="https://github.com/marco-cortes/todo-app" />
                </SwiperSlide>

                <SwiperSlide>
                    <PortfolioItem
                        img={spring}
                        title="Control de Clientes"
                        desc="Aplicacin desarrollado con Spring y Thymeleaf, encargada de llevar un control de clientes."
                        link="https://marco-cortes-mern.herokuapp.com/"
                        repo="https://github.com/marco-cortes/proyecto-ventas" />
                </SwiperSlide>

                <SwiperSlide>
                    <PortfolioItem
                        img={bookmark}
                        title="Bookmark"
                        desc="Landing page basada en un diseo de FrontendMentor."
                        link="https://marco-cortes.me/bookmark"
                        repo="https://github.com/marco-cortes/bookmark" />
                </SwiperSlide>
            </Swiper>
        </section>
    )
}
